const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			cityInfo: {},
			see_sticky: '',
			list: [],
			page: '1',
			pageList: '1',
			tipList: [],
			total: ''
		};
	},
	onLoad(e) {
		console.log(e);
		this.imgUrl = helper.imgUrl;
		uni.setNavigationBarTitle({
			title: e.title
		});
		this.cityInfo = e;
		if(this.cityInfo.id == '38'){
			console.log(this.cityInfo)
			this.getBordList()
			this.getSee_stickyList()
		}else{
			this.getList();
			this.see_stickyList();
		}
	},
	methods: {
		// 发布
		getPost() {
			uni.navigateTo({
				url: `/pages/experience`
			});
		},
		// 置顶加载更多
		getMore() {
			this.page++;
			if(this.cityInfo.id == '38'){
				this.getSee_stickyList()
			}else{
				this.see_stickyList();
			}
		},
		onReachBottom(){
			this.pageList++;
			if(this.cityInfo.id == '38'){
				this.getBordList()
			}else if(this.cityInfo.id != '38'){
				this.getList()
			}
			
		},
		// 跳转帖子详情
		getDateil(e) {
			uni.navigateTo({
				url: `/pages/articleDetail?id=${e}`
			});
		},
		// 帖子列表
		getList() {
			uni.request({
				url: `${helper.requestUrl}/posts/city-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					city_id: this.cityInfo.id,
					see_sticky: '0', //帖子
					page: this.pageList,
					page_size: '10'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					}
				}
			});
		},
		see_stickyList() {
			uni.request({
				url: `${helper.requestUrl}/posts/city-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					city_id: this.cityInfo.id,
					see_sticky: '1', //置顶
					page: this.page,
					page_size: '3'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.total = res.data.total;
						this.tipList = this.tipList.concat(res.data.data);
					}
				}
			});
		},
		getBordList() {
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					see_sticky: '0', //帖子
					board_id: this.cityInfo.id,
					bank_id: '0',
					child_id: '0',
					page: this.pageList,
					page_size: '10'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					}
				}
			});
		},
		// 网友交流
		getSee_stickyList() {
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					board_id: this.cityInfo.id,
					bank_id: '0',
					child_id: '0',
					see_sticky: '1', //置顶
					page: this.page,
					page_size: '3',
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.total = res.data.total;
						this.tipList = this.tipList.concat(res.data.data);
					}
				}
			});
		}
	}
};
